//ES6 syntax
const HeroImage = ({ image, title, text }) => (//es6 syntax of defining the props value inside ({})
    <Wrapper image={image} >
        {/* <div className="image2"><Wrapper image={image} ></Wrapper></div> */}
        <Content>
            <Text>
                <h1>{title}</h1>
                <p>{text}</p>
            </Text>
        </Content>
    </Wrapper>
);

export default HeroImage;