import { useState, useEffect, useRef } from "react";
//API
import API from '../API';


const initialState = {
    page: 0,
    results: [],
    total_pages: 0,
    total_results: 0,
}

export const useHomeFetch = () => {
    //states
    const [SearchTerm, setSearchTerm] = useState('');
    const [state, setState] = useState(initialState); // initial state, we came name state as any eg: can name it as cool :- const [cool, setCool] = useState();
    const [loading, setLoading] = useState(false); //is use when loading
    const [error, setError] = useState(false); // is used when error from the movie database

console.log(SearchTerm);

    const fetchMovies = async (page, searchTearm = "") => {
        try {//setting the states when render
            setError(false);
            setLoading(true);

            const movies = await API.fetchMovies(searchTearm, page);
            console.log(movies);
            //setting the state 
            setState(prev => ({
                ...movies,//importing all the data from movies and spread it here
                results://results is that, where all the listed movies are there :: from the database
                    page > 1 ? [...prev.results, ...movies.results] : [...movies.results] //this is the idf statement (shorted) were if load more is clicked, updating (appending) the page with old movies and new movies
            }))

        } catch (error) {
            setError(true);
        }

        setLoading(false);
    };

    //initial render
    useEffect(() => {
        fetchMovies(1);
    }, []);

    return { state, loading, error, setSearchTerm };
};