import styled from "styled-components";

export const Image = styled.img`

width: 100%;
max-width: 720px;
transition: all 0.3s;
object-fit: cover;
border-radius: 30px;
animation: animateThumb 5s;


:hover {
    opacity: 0.75;
    transform: translatey(-10px) translatex(-5px) scale(1.05);
    transition: all 0.5s;

    
}

@keyframes animateThumb {
    from{
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}


`;